import styled from "styled-components";
import { SectionTitle } from "../../../components/SectionTitle";
import { Thema } from "../../../styles/Thema";
import { FlexWrapper } from "../../../components/FlexWrapper";
import { UlLists } from "./sectionComponents/UlLists";


export const SectionsOneMobile = () => {
  return (
    <StyledSections id={"blog"}>
      <SectionTitle
        SectionTitlePrimery="  - "
        SectionTitleGreen=" "
        align="center"
        direction="column"
        colorline={`${Thema.colors.fontGreen}`}
      ></SectionTitle>
      
      <FlexWrapper align="center" justify="space-between" direction="column">
        <div>
          <UlListLeft className="UlListLeft">
            <UlLists
              text="   "
              TopLiName=" "
              BottomLiName=" DONATONI"
              NumberOfCircle={0o1}
              bordertextposition
            ></UlLists>
            <UlLists
            text="           .   ,         ,                  "
              TopLiName=""
              BottomLiName=""
              NumberOfCircle={0o2}
              bordertextposition
            ></UlLists>
            <UlLists
            text="         : , , , , , ."
              TopLiName=""
              BottomLiName=""
              NumberOfCircle={0o3}
              bordertextposition
            ></UlLists>
          </UlListLeft>
        </div>
        
        <div>
          <UlListRight className="UlListRight">
            <UlLists
            text="   -        , , ,      ."
            justifyContent
            alignItems
            withRowReverse
              TopLiName=""
              BottomLiName=" "
              NumberOfCircle={0o4}
              
            ></UlLists>
            <UlLists
            text="         .           ."
            justifyContent
            alignItems
            withRowReverse
              TopLiName=" "
              BottomLiName=""
              NumberOfCircle={0o5}
              
            ></UlLists>
            <UlLists
            text="        .    ,  ,   .             ."
            justifyContent
            alignItems
            withRowReverse
              TopLiName="  "
              BottomLiName="   "
              NumberOfCircle={0o6}
              
            ></UlLists>
          </UlListRight>
        </div>
      </FlexWrapper>
    </StyledSections>
  );
};

const StyledSections = styled.section`
  display: none;
  
@media ${Thema.media.tablet} {
    display: block;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 140px;
    background-color: ${Thema.colors.secondaryBg};
  min-height: 100vh;
    li {
    color: ${Thema.colors.accent};
  }
}
`;



const UlListLeft = styled.div`
  &.UlListLeft {
        
  }
`;
const UlListRight = styled.div`
  &.UlListRight {
    
    
  }
`;