import styled from "styled-components";
import { FlexWrapper } from "../../../components/FlexWrapper";
import { SectionTitle } from "../../../components/SectionTitle";
import photo from "../../../assets/images/SectionsPhotoOne.webp";
import { Thema } from "../../../styles/Thema";
import { UlLists } from "./sectionComponents/UlLists";

export const SectionsOne = () => {
  return (
    <StyledSections id={"blog"}>
      <SectionTitle
        SectionTitlePrimery="  - "
        SectionTitleGreen=" "
        align="center"
        direction="column"
        colorline={`${Thema.colors.fontGreen}`}
      ></SectionTitle>
      
      <FlexWrapper align="center" justify="space-between">
        <div>
          <UlListLeft className="UlListLeft">
            <UlLists
              text="   "
              TopLiName=" "
              BottomLiName=" DONATONI"
              NumberOfCircle={0o1}
              bordertextposition
            ></UlLists>
            <UlLists
            text="           .   ,         ,                  "
              TopLiName=""
              BottomLiName=""
              NumberOfCircle={0o2}
              bordertextposition
            ></UlLists>
            <UlLists
            text="         : , , , , , ."
              TopLiName=""
              BottomLiName=""
              NumberOfCircle={0o3}
              bordertextposition
            ></UlLists>
          </UlListLeft>
        </div>
        <div>
          <Photo src={photo}></Photo>
        </div>
        <div>
          <UlListRight className="UlListRight">
            <UlLists
            text="   -        , , ,      ."
            justifyContent
            alignItems
            withRowReverse
              TopLiName=""
              BottomLiName=" "
              NumberOfCircle={0o4}
              
            ></UlLists>
            <UlLists
            text="         .           ."
            justifyContent
            alignItems
            withRowReverse
              TopLiName=" "
              BottomLiName=""
              NumberOfCircle={0o5}
              
            ></UlLists>
            <UlLists
            text="        .    ,  ,   .             ."
            justifyContent
            alignItems
            withRowReverse
              TopLiName="  "
              BottomLiName="   "
              NumberOfCircle={0o6}
              
            ></UlLists>
          </UlListRight>
        </div>
      </FlexWrapper>
    </StyledSections>
  );
};

const StyledSections = styled.section`
  padding-top: 140px;
  background-color: ${Thema.colors.secondaryBg};
  min-height: 100vh;
  li {
    color: ${Thema.colors.accent};
  }
  @media ${Thema.media.tablet} {
    display: none;
  }
`;

const Photo = styled.img`
  width: 1003pxpx;
  height: 575px;
`;

const UlListLeft = styled.div`
  &.UlListLeft {
        padding-left: 70px;
  }
`;
const UlListRight = styled.div`
  &.UlListRight {
    
    padding-right: 70px;
  }
`;
